@import "../../../node_modules/slick-carousel/slick/slick.css";
@import "../../../node_modules/slick-carousel/slick/slick-theme.css";
.container {
  margin: 0 auto;
  padding: 0px;
  width: 100%;
  color: #333;
}

.content {
  padding: 0px;
  margin: auto;
  width: 100%;
}
.slick-slide .image {
  padding: 0px;
}
.slick-slide img {
  display: block;
  margin: auto;
}
.slick-slide img.slick-loading {
  border: 0;
}
.slick-slider {
  margin: 0px auto 0px;
}
.slick-dots {
  margin-left: 0;
  bottom: 79px;
}
.slick-dots li button:before {
  font-size: 15px;
}
.slick-thumb {
  bottom: -45px;
}
.slick-thumb li {
  width: 60px;
  height: 45px;
}
.slick-thumb li img {
  filter: grayscale(100%);
}
.slick-thumb li.slick-active img {
  filter: grayscale(0%);
}
@media (max-width: 768px) {
  .center {
    margin-left: -20px;
    margin-right: -20px;
  }
}

.slick-arrow {
  background-color: grey;
  /* color: black; */
}
.slick-arrow:hover {
  background-color: grey;
}
